.tableberg-screen-center {
    padding: 0 !important;

    .tableberg-screen-center-vertical {
        position: absolute;
        top: 0;
        width: 50%;
        height: 100%;
        border-right: 1px solid red;
        z-index: 10000;
        padding: 0 !important;
    }

    .tableberg-screen-center-horizontal {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 50%;
        border-top: 1px solid red;
        z-index: 10000;
        padding: 0 !important;
    }
}
